import { FC } from 'react';
import { Box, Grid, InputAdornment, TextField } from '@mui/material';
import { AccountCircle } from '@mui/icons-material';
import Header from '@/components/oraganisms/Header/Header';
import SideBar from '@/components/oraganisms/SideBar/SideBar';
import BarcodeReader from '@/components/oraganisms/BarcodeReader/BarcodeReader';
import ComfirmDialog from '@/components/molecules/ConfirmDialog/ConfirmDialog';

type Props = {
  drawerOpen: boolean;
  toggleDrawer: () => void;
  onLogout: () => void;
  patientId: string;
  orderHeaderId: string;
  onScan: (code: string) => void;
  dialogOpen: boolean;
  setDialogOpen: (dialogOpen: boolean) => void;
  transit: () => void;
  onChangePatientId: (patientId: string) => void;
  onChangeOrderHeaderId: (orderHeaderId: string) => void;
};

const InjectionCheckPresenter: FC<Props> = ({
  drawerOpen,
  patientId,
  orderHeaderId,
  dialogOpen,
  toggleDrawer,
  onLogout,
  onScan,
  setDialogOpen,
  transit,
  onChangePatientId,
  onChangeOrderHeaderId: onChangeOrderId,
}) => (
  <>
    <Header onDrawerToggle={toggleDrawer} />
    <SideBar
      drawerOpen={drawerOpen}
      toggleDrawer={toggleDrawer}
      onOpen={onLogout}
    />
    <ComfirmDialog
      dialogOpen={dialogOpen}
      message={`ID${patientId}
      ID${orderHeaderId}`}
      onClickCancel={() => {
        setDialogOpen(false);
      }}
      onClickOK={() => {
        transit();
      }}
      onClose={() => {
        setDialogOpen(false);
      }}
    />
    <Box
      paddingTop="25px"
      sx={{
        flexGrow: 1,
      }}
    >
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <TextField
            id="patient-id"
            label="ID"
            color="success"
            value={patientId}
            focused={!!patientId}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AccountCircle />
                </InputAdornment>
              ),
            }}
            fullWidth
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              onChangePatientId(event.target.value);
            }}
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            id="order-id"
            label="ID"
            color="success"
            value={orderHeaderId}
            focused={!!orderHeaderId}
            fullWidth
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              onChangeOrderId(event.target.value);
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <BarcodeReader onScan={onScan} />
        </Grid>
      </Grid>
    </Box>
  </>
);

export default InjectionCheckPresenter;
