import { VFC } from 'react';
import SplitButtonPresenter from './SplitButtonPresenter';

type Props = {
  options: string[];
  onClick: (index: number) => void;
  defaultSelectedIndex?: number;
  fixed?: boolean;
  right?: number;
  bottom?: number;
};

const SplitButton: VFC<Props> = ({
  options,
  onClick,
  defaultSelectedIndex,
  fixed,
  right,
  bottom,
}) => (
  <SplitButtonPresenter
    options={options}
    onClick={onClick}
    defaultSelectedIndex={defaultSelectedIndex}
    fixed={fixed}
    right={right}
    bottom={bottom}
  />
);

export default SplitButton;
