import { FC, useCallback, useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { isAfter } from 'date-fns';
import ky from '@/lib/ky';

import OneShotExec from './Oneshot/OneShotExec';
import DropExecute from './Drop/DropExecute';
import Header from '@/components/oraganisms/Header/Header';
import { InjectionOrder } from '@/types/order/injection/injectionOrder';
import ConfirmDialog from '@/components/molecules/ConfirmDialog/ConfirmDialog';
import { useAuthUser } from '@/contexts/auth/AuthUserContext';

const InjectionExecute: FC = () => {
  const auth = useAuthUser();
  const navigate = useNavigate();
  const { patientId, orderHeaderId } = useParams();

  const [injectionOrderData, setInjectionOrderData] =
    useState<InjectionOrder>();

  const [errorText, setErrorText] = useState<string | undefined>(undefined);
  const [dialogOpen, setDialogOpen] = useState<boolean>(false);

  const load = useCallback(async (): Promise<void> => {
    try {
      const result = await ky
        .get('orders/injections', {
          searchParams: {
            orderHeaderId: Number(orderHeaderId),
          },
        })
        .json<InjectionOrder>();

      const checkOrder = (): boolean => {
        if (!result) {
          setErrorText('');

          return false;
        }

        if (result.patientId !== patientId) {
          setErrorText('');

          return false;
        }

        if (result.mainClass !== 'injection') {
          setErrorText('');

          return false;
        }

        if (result.orderStatusName === '') {
          setErrorText('');

          return false;
        }

        if (result.orderStatus === 5) {
          setErrorText('');

          return false;
        }

        if (!result.executableFutureDay) {
          const preferredDate = new Date(result.preferredDate);
          if (isAfter(preferredDate, new Date())) {
            setErrorText('');

            return false;
          }
        }

        if (result.instructedStatus === 1) {
          if (result.forcingInstracted) {
            setDialogOpen(true);

            return true; // true
          }
          setErrorText('');

          return false;
        }

        return true;
      };

      setInjectionOrderData(checkOrder() ? result : undefined);
    } catch (err) {
      throw new Error('error');
    }
  }, [orderHeaderId, patientId]);

  useEffect(() => {
    void load();
  }, [load]);

  if (!injectionOrderData) {
    return <p>{errorText}</p>;
  }

  const instructOrder = async (
    injectionOrder: InjectionOrder,
  ): Promise<void> => {
    try {
      const result = await ky
        .put(`orders/injections`, {
          json: {
            orderHeaderId: injectionOrder.orderHeaderId,
            userId: auth?.staffId,
            patientId: injectionOrder.patientId,
          },
          timeout: false,
        })
        .json<InjectionOrder>();

      setInjectionOrderData(result);
    } catch {
      // eslint-disable-next-line no-alert
      alert('error');
    }
  };

  return (
    <>
      <Header
        onDrawerToggle={() => {
          console.log(123);
        }}
      />

      <ConfirmDialog
        dialogOpen={dialogOpen}
        message={`
        `}
        onClickCancel={() => {
          navigate(-1);
        }}
        onClickOK={async () => {
          await instructOrder(injectionOrderData);
        }}
        onClose={() => {
          setDialogOpen(false);
        }}
      />

      {injectionOrderData.execType === 2 ? (
        <DropExecute injectionOrder={injectionOrderData} />
      ) : (
        <OneShotExec injectionOrder={injectionOrderData} />
      )}
    </>
  );
};

export default InjectionExecute;
