import { VFC } from 'react';
import { Navigate, Route, Routes, Outlet } from 'react-router';
import { BrowserRouter } from 'react-router-dom';
import Login from './components/pages/Login/Login';
import MainMenu from './components/pages/MainMenu/MainMenu';
import InjectionCheck from './components/pages/InjectionCheck/InjectionCheck';
import InjectionExecute from './components/pages/InjectionExecute/InjectionExecute';
import { useAuthUser } from './contexts/auth/AuthUserContext';
import BarcodeReader from './components/oraganisms/BarcodeReader/BarcodeReader';
import TranfusionExecute from './components/pages/TransfusionExecute/TransfusionExecute';
import TransfusionCheck from './components/pages/TransfusionCheck/TransfusionCheck';
import AppProvider from './contexts/AppProvider';
import Header from './components/oraganisms/Header/Header';
import SideBar from './components/oraganisms/SideBar/SideBar';
import useDisclosure from './hooks/useDisclosure';

/**
 *  Outlet
 * @returns :<Outlet />, :/login 
 */
const PrivateOutlet = () => {
  const authUser = useAuthUser();
  const isAuthenticated = authUser != null;

  const { isOpen, open, toggle } = useDisclosure();

  return isAuthenticated ? (
    <>
      <Header onDrawerToggle={toggle} />
      <SideBar drawerOpen={isOpen} toggleDrawer={toggle} onOpen={open} />
      <Outlet />
    </>
  ) : (
    <Navigate to="/login" />
  );
};

/**
 *  Outlet
 * @returns :/main , :<Outlet />
 */
const UnAuthOutlet = () => {
  const auth = useAuthUser();
  const isAuthenticated = auth?.loggedIn;

  return isAuthenticated ? <Navigate to="/main" /> : <Login />;
};

const App: VFC = () => (
  <AppProvider>
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        <Route path="/" element={<PrivateOutlet />}>
          <Route path="/" element={<InjectionCheck />} />
          <Route path="/barcode" element={<BarcodeReader />} />
          <Route path="/main" element={<MainMenu />} />
          <Route path="/injectionexecutedrop" element={<InjectionExecute />} />
          <Route
            path="/injectionexecute/:patientId/:orderHeaderId"
            element={<InjectionExecute />}
          />
          <Route path="/transfusioncheck" element={<TransfusionCheck />} />
          <Route path="/transfusion" element={<TranfusionExecute />} />
        </Route>
        <Route path="/login" element={<UnAuthOutlet />}>
          <Route path="/login" element={<Login />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </AppProvider>
);

export default App;
